@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  font-size: 16px;
  color: black;
  background: white;
  font-family: 'Fira Sans', sans-serif;
}

.App-header {
  padding: 15px 0px 15px 0px;
  margin-bottom: 15px;
  background: #f78888;
  font-size: 20px;
}

.App-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 0px 20px 0px 20px;
}

.App-view {
  border: 3px solid #f3d250;
  border-radius: 10px;
  padding: 15px 20px 15px 20px;
}

.Visualize {
  display: flex;
  flex-direction: column;
}

.Visualize-title {
  font-weight: bold;
  margin-bottom: 20px;
}

.Visualize-section {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 5fr;
}

.Visualize-selections {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.Visualize-parameter {
  margin: 20px 0px 20px 0px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 2fr;
}

.Visualize-lengths {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.selected {
  background: #f3d250;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
}

.selection {
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
}

.selection:hover {
  cursor: pointer;
}

.Visualize-plot {
  height: 360px;
  width: 560px;
}

.Visualize-info {
  padding-top: 80px;
  font-style: italic;
  font-size: 16px;
}

.Explore {
  display: flex;
  flex-direction: column;
}

.Explore-title {
  font-weight: bold;
  margin-bottom: 20px;
}

.Explore-section {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 5fr;
}

.Explore-selections {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.selected {
  background: #f3d250;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.selection {
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #5da2d5;
}

.selection:hover {
  cursor: pointer;
}

.Explore-words {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 20px 0px 20px 0px;
}

.Words-left {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.Words-input {
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  background: #f3d250;
  font-size: 12px;
}

.Words-input:focus,
input:focus {
  outline: none;
}

.Words-submit {
  padding: 5px 20px 5px 20px;
  border-radius: 5px;
  background: #f78888;
}

.Words-submit:hover {
  cursor: pointer;
}

.Explore-poem {
  background: white;
  height: 320px;
  overflow-y: scroll;
  color: black;
  padding: 25px 40px 25px 40px;
}

.poem-info {
  font-style: italic;
  padding-top: 55px;
  font-size: 16px;
}

